.project-container {
    width: 1000px;
    margin-left: 20%;
    /* margin-top: 8%; */
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 5%;
    
}
.project-title {
    font-family: 'Open Sans';
    font-size: 18px;
    /* text-align: center;   */
    color: #000000;
    /* margin-left: 20%; */
    margin-top: 40%;

}
.projects-descript {
    font-family: 'Open Sans';
    font-size: 18px;
    /* text-align: center;   */
    color: #000000;
    margin-left: 20%;
    margin-top: 40%;

}
.project-card-container {
    width: 380px;
    height: 250px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    margin: 5px;
    -webkit-transition: 0.3s ease;
    transition: 0.3s ease;


}

.project-container img {
    width: 380px;
    height: 100%;
    object-fit: cover;
}

.description{
    width: auto;
    top: 10%;
    padding: 10px;
    padding-left: 20px;
    opacity: 0;
    background: rgba(255,255,255,.85);
    color: #505050;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    -webkit-transition: opacity 0.2s 0.1s, -webkit-transform 0.2s 0.1s;
    transition: opacity 0.2s 0.1s, -webkit-transform 0.2s 0.1s;
    transition: transform 0.2s 0.1s, opacity 0.2s 0.1s;
    transition: transform 0.2s 0.1s, opacity 0.2s 0.1s, -webkit-transform 0.2s 0.1s;
    font-family: 'Open Sans';
    font-size: 11px;
}

.description-text {
    width: 100%;

}

.project-card-container:hover .description{
    cursor: pointer;
    opacity: 1;
    -webkit-transform: translateY(-104.4%);
            transform: translateY(-104.4%);
}

.project-card-container:hover{
    box-shadow: 10px 10px 25px rgba(0,0,0,0.3);
    -webkit-transform: scale(1.1,1.1);
            transform: scale(1.1,1.1);
} 
 img:hover{
    cursor: pointer;
}



.about-container {
    width: 700px;
    margin-left: 5%;
}

.about-intro-container {
    width: 850px;
    display: flex;
    margin-left: 20%;
    margin-top: 3%;
    font-family: 'Open Sans';
    font-size: 14px;

}

.nav-container {
    width: 700px;
    height: 50px;
    margin-left: 20%;
    margin-top: 3%;
    padding: 40px 0 10px;
    right: 25%;
}

.logo {
    font-family: 'Helvetica Neue';
    font-size: 25px;
    font-weight: bold;
    color: #3d3d3d;
    text-decoration: none;
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
}

.nav-item-container {
    display: flex;
}

.nav-item {
    display: flex;
    margin-left: 75%;
    width: 300px;
}

.nav-link {
    margin-left: 15%;
    margin-left: 15%;
    font-family: 'Helvetica Neue';
    font-weight: bold;
    font-size: 18px;
    text-decoration: none;
    color: #3d3d3d;
    -webkit-transition: 0.1s ease;
    transition: 0.1s ease;
}

.nav-link:hover {
    color: #9b9b9b;
}

.logo:hover {
    color: #9b9b9b;
}

.footer-container {
    margin-left: 20%;
    bottom: 0;
    display: fixed;


}

.footer-flex {

    display: flex;
}

.footer-item {
    margin: 8px;
    height: 20px;
}
.icon {
    height: 50px;
}

a {
    color:#3d3d3d;
}

.linkedin, .git, .email {
    -webkit-transition: 0.2s ease ;
    transition: 0.2s ease ;
}
.linkedin:hover{
    color: #0077B5;
}

.git:hover{
    color:#040204;
}

.email:hover {
    color: #d44638;
}
